.courses-v23__modules-accordion {
    @include media('>=large') {
        display: flex;
        column-gap: $measure--two-tenths;

        .accordion-v23 {
            width: 50%;
        }
    }
}

.js {
    .courses-v23__modules-accordion {
        a {
            color: $color-white;
            border-bottom-color: $color-white;
            background-color: rgba(30, 30, 30, 0.5);

            &:hover,
            &:active,
            &:focus {
                color: $color-black;
                background-color: $color-white;
            }

            &:active,
            &:focus {
                outline-offset: $border-width-thick-w20;
                outline-color: $color-white;
            }
        }
    }
}
