.courses-v23__careers__ksp {
    grid-column: 1/13;
}

.mba-courses-v23 {
    .ksp-mba-v23.ksp-mba-v23--graphs {
        .ksp-mba-v23__list__item__value {
            stroke: $color-mba-accent-v23;
        }
    }
}

.ksp-mba-v23 {
    position: relative;
    grid-column: 1/13;
    margin-top: $measure--small2;
    margin-bottom: $measure--small2;

    .ksp-mba-v23__list {
        display: flex;
        flex-direction: row;
        gap: $measure--basic;
        flex-wrap: wrap;

        &[data-count='1'],
        &[data-count='2'],
        &[data-count='3'] {
            .ksp-mba-v23__list__item {
                flex-basis: 25%;

                @include media('<=between') {
                    flex-basis: 50%;
                }
            }
        }

        &[data-count='4'] {
            .ksp-mba-v23__list__item {
                flex-basis: 23%;

                @include media('<=between') {
                    flex-basis: 50%;
                }
            }
        }
    }

    .ksp-mba-v23__list__item__label {
        p {
            font-size: $font-size--small-089R;
            line-height: $line-height-augmented-fourth;
            letter-spacing: inherit;
        }
    }

    &.ksp-mba-v23--graphs {
        .ksp-mba-v23__list {
            &[data-count='1'],
            &[data-count='2'],
            &[data-count='3'],
            &[data-count='4'] {
                .ksp-mba-v23__list__item {
                    @include media('<=between') {
                        flex-basis: 48%;
                    }
                }
            }
        }

        .ksp-mba-v23__list__item__value {
            position: relative;
            width: 100%;

            path {
                @extend %animate--svg-path-target;
            }
        }

        .ksp-mba-v23__list__item__value__number {
            position: absolute;
            display: flex;
            top: 0;
            width: 100%;
            height: 100%;
            justify-content: center;
            align-items: center;
            font-size: $font-size-bayes-banner;
            color: $color-mba-lead;
            @include font-family(meta);
        }

        .ksp-mba-v23__list__item__value__number__unit {
            font-size: $font-size-alpha;
        }

        .ksp-mba-v23__list__item__label {
            color: $color-emperor;
            text-align: center;
            margin-top: $measure--half;
        }
    }

    &.ksp-mba-v23--numbers {
        padding: $measure--small2;

        @include media('<=medium') {
            padding: $measure--small2;
        }

        .ksp-mba-v23__heading {
            margin-top: $measure--basic;
        }

        .ksp-mba-v23__list {
            gap: 0;
        }

        .ksp-mba-v23__list__item {
            position: relative;
            display: flex;
            flex-direction: row;
        }

        .ksp-mba-v23__list__item:not(:last-child) {
            &:after {
                content: '';
                width: $measure--small4;
                margin-right: $measure--half;

                background: radial-gradient(
                    ellipse at center,
                    $color-fordgray,
                    $color-fordgray $border-width-hairline-w20,
                    transparent 0,
                    transparent 100%
                );
                background-size: $measure--small1 $measure--small1;
                background-position-x: -$measure--half;
            }
        }

        .ksp-mba-v23__list__item:last-child {
            &:after {
                content: '';
                width: $measure--small4;
                height: 100%;
                background-size: $measure--small1 $measure--small1;
            }
        }

        .ksp-mba-v23__list__item:nth-of-type(2) {
            @include media('<=between') {
                &:after {
                    content: inherit;
                }
            }
        }

        .ksp-mba-v23__list__item__container {
            flex: 1;
        }

        .ksp-mba-v23__list__item__value {
            display: flex;
            flex-direction: row;
            align-items: center;

            .ksp-mba-v23__list__item__value__number {
                font-size: $font-size-bayes-banner;
                color: $color-black;
                @include font-family(meta);
                
            }

            .ksp-mba-v23__list__item__value__unit {
                font-size: $font-size-alpha;
            }
        }
    }
}
